html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

*,
*::before,
*::after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit; }

body {
  position: relative;
  display: grid;
  grid-template-rows: -webkit-min-content 1fr -webkit-min-content;
  grid-template-rows: min-content 1fr min-content;
  min-width: 320px;
  min-height: 100vh;
  margin: 0 auto;
  padding: 0;
  font-family: "Poppins", "Tahoma", "Geneva", sans-serif;
  font-size: 16px;
  line-height: 21px;
  font-weight: 500;
  font-style: normal;
  text-align: left;
  color: #1d1613;
  background-color: #ffffff; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.page-header {
  position: relative;
  font-family: "Poppins", "Tahoma", "Geneva", sans-serif;
  font-size: 16px;
  line-height: 21px;
  font-weight: 500;
  font-style: normal;
  color: #ffffff;
  background-color: #ffffff; }
  .page-header::after {
    content: "";
    position: absolute;
    top: 115px;
    left: 0;
    width: 100%;
    height: 1px;
    background-color: #f6f5f4; }
  .page-header a {
    color: #1d1613; }
    .page-header a:hover, .page-header a:focus {
      color: #a87b62;
      outline-color: #a87b62; }
    .page-header a:active {
      color: #a87b62;
      opacity: 0.6; }
  .page-header--catalog::after, .page-header--card::after {
    top: 125px; }
  .page-header--card::after {
    -webkit-box-shadow: 0 61px 0 0 #f6f5f4;
            box-shadow: 0 61px 0 0 #f6f5f4; }

.page-header__wrapper {
  margin: 0 auto;
  padding: 0 20px 0 20px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column; }
  @media (min-width: 320px) {
    .page-header__wrapper {
      width: auto; } }
  @media (min-width: 768px) {
    .page-header__wrapper {
      width: auto; } }
  @media (min-width: 1366px) {
    .page-header__wrapper {
      width: 1366px; } }
  @media (max-width: 1023px) {
    .page-header__wrapper {
      padding: 0 25px 0 22px; } }
  @media (max-width: 767px) {
    .page-header__wrapper {
      padding: 0 15px 0 16px; } }

.page-header__logo-wrapper {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between; }

.page-header__toggle {
